.stars {
  display: flex;

  &__item {
    width: 15px;
    height: 15px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;
  }

}

.stars--4 .stars__item:nth-child(-n+4) {
  background-image: url("../images/star-active.svg");
}
